import React from "react";
import CountUp from 'react-countup';
import { useEffect, useState } from 'react';
import { NavLink } from "react-router-dom";

export default function Placements() {

    const [startCounter, setStartCounter] = useState(false);
  
    useEffect(() => {
      const handleScroll = () => {
        const section = document.getElementById("counter-section");
        if (section) {
          const rect = section.getBoundingClientRect();
          if (rect.top < window.innerHeight && rect.bottom >= 0) {
            setStartCounter(true);
          }
        }
      };
  
      window.addEventListener("scroll", handleScroll);
      return () => window.removeEventListener("scroll", handleScroll);
    }, []);

  return (
    <div>
      <div
        className="container-fluid px-0 overflow-hidden page-banner"
        style={{ background: "url(/images/placements.webp)" }}
      >
        <nav aria-label="breadcrumb">
          <ol className="breadcrumb">
            <li className="breadcrumb-item">Placements</li>
            {/* <li className="breadcrumb-item active" aria-current="page">
              <strong>Controller of Examinations</strong>
            </li> */}
          </ol>
        </nav>
      </div>

      {/* <section className="py-5">
        <div className="container text-center">
          <div className="row">
            <div className='col one px-4'>
              <h1 className="fs-1 text-primary fw-bold">Overview</h1>
              <p>
              Placements at Amrita have always been excellent. Students who are
              eligible and opted for placements have more than one job offer, on
              an average. Exercising their dream option, some of them have up to
              three job offers by the end of their course. A total of 261
              eligible students were placed at Amrita School of Business (ASB)
              at salaries that have shown significant growth over the previous
              years. The placements have provided them high-quality, diverse and
              professional opportunities.
              </p>
            </div>
          </div>
        </div>
      </section> */}

      <section className="py-5 placements">
        <div className="container">
          <div className="row">
            <div className="col">
              <nav>
                <div className="nav nav-tabs mb-3" id="nav-tab" role="tablist">
                  <button className="nav-link active" id="nav-overview-tab" data-bs-toggle="tab" data-bs-target="#nav-overview" type="button" role="tab" aria-controls="nav-overview" aria-selected="true">Placements Overview</button>
                  <button className="nav-link" id="nav-statistics-tab" data-bs-toggle="tab" data-bs-target="#nav-statistics" type="button" role="tab" aria-controls="nav-statistics" aria-selected="false">Statistics</button>
                  <button className="nav-link" id="nav-training-tab" data-bs-toggle="tab" data-bs-target="#nav-training" type="button" role="tab" aria-controls="nav-training" aria-selected="false">Placement Training</button>
                  <button className="nav-link" id="nav-process-tab" data-bs-toggle="tab" data-bs-target="#nav-process" type="button" role="tab" aria-controls="nav-process" aria-selected="false">Recruiting Process</button>
                  <button className="nav-link" id="nav-partner-tab" data-bs-toggle="tab" data-bs-target="#nav-partner" type="button" role="tab" aria-controls="nav-partner" aria-selected="false">Recruiting Partners</button>
                  <button className="nav-link" id="nav-industr-tab" data-bs-toggle="tab" data-bs-target="#nav-industr" type="button" role="tab" aria-controls="nav-industr" aria-selected="false">Industry Collaborations</button>
                </div>
              </nav>
              <div className="tab-content p-3" id="nav-tabContent">
                <div className="tab-pane fade active show" id="nav-overview" role="tabpanel" aria-labelledby="nav-overview-tab">
                  <div className="row d-flex align-items-center">
                    <div className="col-lg-8 three">
                      <h1 className="fs-1 text-primary fw-bold">
                        Placement Overview
                      </h1>
                      <p className="text-justify mb-0">
                        At Vishwa Vishwani School of Business we are dedicated
                        to bridging the gap between your academic journey and
                        professional aspirations. Our mission is to provide you
                        with opportunities that align with your career goals and
                        prepare you to thrive in the competitive corporate
                        world. We encourage you to: 
                        Stay proactive by participating in training sessions and workshops.
                        Keep track of placement updates via the portal and emails.
                        Be prepared, professional, and committed to your placement journey.
                      </p>
                      <p className="text-justify">
                        The Placement Office is here to guide, support, and
                        mentor you every step of the way. Together, lets pave
                        the path to your success. Wishing you all the best in
                        your career endeavours!
                      </p>
                    </div>
                    <div className="col-lg-4">
                      <img
                        src="/images/placement-view.webp"
                        className="w-100"
                      />
                    </div>
                  </div>
                  <div className="row">
                    <div className="col-lg-12">
                      <div className="card p-3 bg-grey">
                        <h5>Message !</h5>
                        <p>
                        Dear Students,<br/>
                        Welcome to the Placement Office! <br/>
                        At Vishwa Vishwani School of Business we are dedicated to bridging the gap between your academic journey and professional aspirations. Our mission is to provide you with opportunities that align with your career goals and prepare you to thrive in the competitive corporate world.
                        <br/>We encourage you to:
                        <br/>&nbsp;&nbsp;	Stay proactive by participating in training sessions and workshops.
                        <br/>&nbsp;&nbsp;	Keep track of placement updates via the portal and emails.
                        <br/>&nbsp;&nbsp;	Be prepared, professional, and committed to your placement journey.
                        <br/>The Placement Office is here to guide, support, and mentor you every step of the way. Together, lets pave the path to your success.
                        <br/> Wishing you all the best in your career endeavours!
                        </p>
                      </div>
                    </div>
                  </div>
                  <div className="row three mt-5">
                    <div className="col">
                      <h1 className="text-primary mb-4 ps-0">
                        Placement Officer
                      </h1>
                      <div className="col-lg-12">
                        <div className="img-group">
                          <img src="/images/dummy-pic.webp" />
                          <div className="img-text">
                            <h2 className="mb-4">
                              Dr. SV Suresh Raju
                              <sub className="text-primary fs-5"> MBA</sub>
                            </h2>
                            <p>Placement Officer</p>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>

                <div className="tab-pane fade" id="nav-statistics" role="tabpanel" aria-labelledby="nav-statistics-tab">
                  <p>
                  Statistics
                  </p>
                </div>

                <div className="tab-pane fade" id="nav-training" role="tabpanel" aria-labelledby="nav-training-tab">
                  <p>
                    Placement Training
                  </p>
                </div>

                <div className="tab-pane fade" id="nav-process" role="tabpanel" aria-labelledby="nav-process-tab">
                  <p>
                    Recruiting Process
                  </p>
                </div>

                <div className="tab-pane fade" id="nav-partner" role="tabpanel" aria-labelledby="nav-partner-tab">
                  <p>
                    Recruiting Partners
                  </p>
                </div>
                
                <div className="tab-pane fade" id="nav-industr" role="tabpanel" aria-labelledby="nav-industr-tab">
                  <p>
                    Industry Collaborations
                  </p>
                </div>

              </div>
            </div>
          </div>
        </div>
      </section>

      <section className="py-5 bg-grey" id="counter-section">
        <div className="container">
          <div className="row">
            {[
              { number: 40, label: "New Recruiters" },
              { number: 115, label: "Recruiters" },
              { number: 833000, label: "Average Salary" },
              { number: 760000, label: "Highest Salary" },
              { number: 356, label: "No. of Offers" },
            ].map((item, index) => (
              <div key={index} className="col">
                <div className="counters">
                  <span className="counter-number">
                    {startCounter && (
                      <CountUp start={0} end={item.number} duration={3} />
                    )}
                  </span>
                  <h4 className="mt-2">{item.label}</h4>
                </div>
              </div>
            ))}
          </div>
        </div>
      </section>
    </div>
  );
}
